.flexbox{
    flex-direction: column;
    justify-content: start;
    align-items: center;
}
.tutorial_arrow{
    display: flex;
    flex-direction: column;
    font-family: Arial, Helvetica, sans-serif;
}
.arrow{
    text-align: center;
    font-size: 80px;
}
.arrow_text{
    font-size: 24px;
