.book_card {
  background: #ffffff;
  padding: 16px;
  padding-bottom: 20px;
  border-bottom: 0.5px solid #d7cfcf;
  display: flex;
  flex-direction: column;
 .book_journal {
    font-size: 15px;
    line-height: 18px;
    color: #a19a9a;
    margin-bottom: 16px;
  }
  .book_title {
    font-size: 20px;
    line-height: 23px;
    max-width: 90%;
    color: #9b9393;
    max-height: 44px;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .bookmark_icon {
    flex: none;
  }
}
.button_area{
  height:50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #F7F7F7;
   .delete_img{
    margin: 0 5px;
  }
   .btn_text{
      font-weight: 500;
      font-size: 11px;
      letter-spacing: 0.75px;
      text-transform: uppercase;
      color: white;
  }
}
.flex_between{
  align-items: center;
}
:root {
  --animate-duration:0.4s;
}