.auth_img{
    background: #000;
}
.wrapper{
    padding: 30% 15% 25px 16%;
}
.login__title{
    font-size: 48px;
    font-weight: bold;
    margin: 0;
}
.login_description{
    font-weight: lighter;
    font-size: 28px;
    color: #BBBBBB;
    margin-bottom: 70px;
    margin: 0;
    margin-bottom: 34%;
}
.withLogin{
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
    margin: 9px 0;
    padding: 0;
    & figcaption{
        display: flex;
        justify-content: center;
        align-items: center;
        background: #6A6A6A;
        width: 232px;
        height: 46px;
        font-weight: bold;
        font-size: 14px;
        text-transform: uppercase;
        color: #FFFFFF;
    }
}
.markets{
    margin-top: 23%;
    background-size: 50%;
}