.acticle {
  //   padding: 30px 20px;

  &_title_container {
    padding: 30px 20px 10px;
    display: flex;
  }
  &_title {
    font-weight: bold;
    font-size: 20px;
    line-height: 23px;
    color: #3d3737;
  }
  &_icon {
    flex: none;
    min-width: 12px;
    align-self: flex-end;
  }
  &_media {
    padding: 11px 20px;
    border-top: 0.5px solid #d7cfcf;
    border-bottom: 0.5px solid #d7cfcf;
    img {
      width: 100%;
    }
  }
  &_content {
    padding: 15px 20px;
    font-size: 16px;
    line-height: 19px;
    color: #000000;
  }
}
.button{
  height: 54px;
  width: 50%;
  background: #fff;
  border: 1px solid #D7CFCF;
  font-weight: 300;
  font-size: 18px;
  color: #9B9393;
  text-transform: uppercase;
}