.bottom_group {
  padding: 0 20px;
  height: 25vh;
}
.auth {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
.auth_header {
  height: 40vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  padding-bottom: 30px;
}
.auth_header_sub {
  font-weight: normal;
  font-size: 17px;
  line-height: 22px;
  text-align: center;
  color: rgba(60, 60, 67, 0.6);
}
.auth_header_title {
  font-weight: bold;
  font-size: 34px;
  line-height: 41px;
  text-align: center;
  color: #000000;
  margin-bottom: 16px;
  margin-top: 16px;
}
.form_group {
  height: 35vh;
}
.text_help {
  font-size: 15px;
  line-height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #8e8e93;
  margin-top: 30px;
}
.link_auth {
  color: #007aff;
  font-weight: 600;
  font-size: 15px;
  line-height: 20px;
  margin-left: 4px;
}
