.input_container {
  padding: 20px 0;
  margin: 0 16px;
  border-bottom: 0.5px solid #00000021;
  display: flex;
}
.regular_input {
  border: none;
  flex: auto;
  outline: none;
  &::placeholder {
    font-size: 17px;
    line-height: 22px;
    color: rgba(60, 60, 67, 0.3);
  }
  &:focus {
    outline: none;
  }
}
