.main_screen {
  height: 100vh;
  overflow: scroll;
}

.menu {
  position: fixed;
  top: 0;
  width: 80vw;
  bottom: 0;
  left: -80vw;

  background-color: white;
  flex-direction: column;
  display: flex;
  transition: all 0.5s ease-in-out;
  // display: none;

  .menu_bg {
    position: absolute;
    background-color: transparent;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
  }
  &.open {
    animation-name: openAni;
    animation-duration: 0.5;
    left: 0;
    display: flex;
  }
  &.close {
    animation-name: closeAni;
    animation-duration: 1s;
    left: -80vw;
  }
  .menu_profile {
    padding-bottom: 24px;
    padding-top: 24px;
    padding-left: 20px;
    padding-right: 20px;
    z-index: 24;
    background-color: white;
  }
  .menu_profile_avatar {
    width: 80px;
    height: 80px;
    border-radius: 50%;
  }
  .menu_profile_info {
    border-left: 1px solid #c4c4c4;
    padding-left: 16px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-top: 16px;
    padding-bottom: 16px;
    width: 45%;
  }
  .menu_profile_info_cat {
    font-size: 10px;
    line-height: 12px;
    text-transform: lowercase;
    color: #000000;
    margin-right: 8px;
  }
  .menu_profile_info_num {
    font-size: 10px;
    line-height: 12px;
    color: #000000;
  }
  .menu_profile_nick {
    font-size: 16px;
    line-height: 19px;
    color: #000000;
    margin-top: 16px;
  }

  .swicher {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    width: 80%;
    transition: 1s;
    .swicher_text {
      font-size: 14px;
      line-height: 16px;
      text-transform: lowercase;
      color: #6c6969;
    }
  }

  .settings {
    background-color: #f7f7f7;
    padding-left: 20px;
    padding-right: 20px;
    z-index: 24;
    background-color: white;
    flex: auto;
    .settings_journal {
      font-size: 15px;
      line-height: 18px;
      color: #6c6969;
      margin-bottom: 38px;
      margin-top: 38px;
      display: flex;
    }
    .settings_subscrip {
      font-size: 15px;
      line-height: 18px;
      color: #302f2f;
      padding: 14px 0;
      border-top: 1px solid #cac7c7;
      border-bottom: 1px solid #cac7c7;
      margin-bottom: 32px;
    }
  }
}

@keyframes openAni {
  from {
    left: -80vw;
  }
  to {
    left: 0vw;
  }
}

@keyframes closeAni {
  from {
    left: 0vw;
    display: flex;
  }
  to {
    left: -80vw;
    display: none;
    visibility: hidden;
  }
}
.ad_container {
  padding: 8px 16px;
  border-bottom: 1px solid #cac7c7;
  text-align: center;
  display: flex;
  align-items: center;
  height: 100px;
}
