.manage_screen {
  @media (max-width: 560px) {
    // background-color: red;

    width: 100%;
    // padding-top: 50px;

    &_tab {
      display: flex;
      padding: 16px 16px;
      background: #f7f7f7;
      position: fixed;
      right: 0;
      left: 0;
      top: 0;
      z-index: 12;
      justify-content: space-between;
      align-items: center;
    }
    .tab_item {
      flex: 1;
      text-align: center;
      justify-content: center;
      display: flex;
      font-size: 15px;
      color: #302f2f;
      line-height: 18px;
      margin-right: 20px;
      &.active {
        font-weight: bold;
      }
    }
  }
}

.manage_screen_content {
  display: flex;
  flex-wrap: wrap;
  padding: 20px;

  // justify-content: space-between;

  .journal_item {
    padding: 5px 7px;
    background: #f7f7f7;
    width: calc((100vw - 56px) / 3);
    display: flex;
    margin-bottom: 16px;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    margin-right: 8px;
    position: relative;

    .selected_container {
      display: flex;
      justify-content: flex-end;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: #80808042;
      padding-right: 13px;
      padding-top: 6px;
    }
    &:nth-child(3n) {
      margin-right: 0;
    }
    .counts {
      background: #c4c4c4;
      opacity: 0.6;
      border-radius: 4px;
      font-size: 10px;
      padding: 2px;
      margin-bottom: 4px;
    }
    img {
      width: 90%;
      height: auto;
      margin-bottom: 12px;
    }
    .journal_item_title {
      font-size: 10px;
      line-height: 12px;
      text-align: center;
      text-transform: capitalize;
      color: #000000;
      margin-bottom: 12px;
      max-width: 100%;
      width: 100%;
      word-wrap: break-word;
      // white-space: nowrap;
      overflow: hidden;
      // text-overflow: ellipsis;
    }
    .journal_item_remove {
      font-weight: bold;
      font-size: 10px;
      line-height: 12px;
      font-variant: small-caps;
      border-top: 1px solid #cac7c7;
      padding-top: 6px;
      color: #6c6969;
      width: 100%;
      text-align: center;
      &.follow {
        color: green;
      }
    }
  }
}
.header_manage {
  height: 50px;
  padding: 0 20px;
  display: flex;
  border-bottom: 1px solid #cac7c7;
  border-top: 1px solid #cac7c7;
  justify-content: space-between;
  align-items: center;
  .header_actions {
    display: flex;
  }
  .header_actions_remove {
    text-align: center;
    justify-content: center;
    display: flex;
    font-size: 15px;
    color: #d60000;
    line-height: 18px;
    font-weight: 500;
    margin-right: 12px;
  }
  .header_actions_reset {
    text-align: center;
    justify-content: center;
    display: flex;
    font-size: 15px;
    color: #0d6917;
    line-height: 18px;
    font-weight: 500;
  }
}
.actions_remove {
  font-size: 14px;
  color: #d60000;
  margin: 0 5px;
}
.actions_reset {
  font-size: 14px;
  color: #0d6917;
  margin: 0 5px;
}

.manage_btn {
  background-color: #56ccf2;
  display: flex;
  width: 180px;
  height: 28px;
  border-radius: 21px;
  align-items: center;
  justify-content: center;
  .manage_btn_text {
    margin-left: 12px;
    font-style: normal;
    font-weight: 500;
    font-size: 11px;
    line-height: 17px;
    color: #302f2f;
  }
}
