.acticle_card {
  border-bottom: 0.5px solid #d7cfcf;
  padding: 20px 16px;
  display: flex;
  flex-direction: row;
  align-items: center;
  &.read {
    background-color: #f0f0f0;
    a,.acticle_card_journal_desc  {
      color: #9B9393;
    }
  }
  &_title {
    font-size: 15px;
    line-height: 18px;
    color: #3d3737;
    margin-bottom: 16px;
    display: block;
  }
  &_journal_title {
    display: block;

    font-weight: bold;
    font-size: 20px;
    line-height: 23px;
    color: #3d3737;
    word-wrap: break-word;
    margin-bottom: 20px;
  }
  &_journal_desc {
    font-size: 14px;
    line-height: 16px;
    color: #000000;
    margin-bottom: 10px;
    overflow: hidden;
    // text-overflow: ellipsis;
    // max-width: 70%;
    // white-space: nowrap;
  }
  &_journal {
    width: 80%;
  }
  &_journal_time {
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    color: #7c7777;
  }
  &_journal_container {
    display: flex;
    justify-content: space-between;
  }
  &_bookmark_icon {
    width: 48px;
    margin: 0 20px 0 14px;
    
  }
}
