.categories_item {
  display: flex;
  //   padding-right: 40px;
  border-bottom: 0.3px solid #302f2fe8;
  background-color: #fafafa;
  align-items: center;
  padding: 20px 29px 20px 29px;
  &:first-child {
    border-top: 0.3px solid #302f2fe8;
  }
  &_title {
    flex: 1;
    text-align: center;
    font-weight: 300;
    font-size: 18px;
    line-height: 20px;
    text-transform: capitalize;
    color: #302f2f;
  }
}
.categories_item {
  &.main_category {
    background-color: #f7f7f7;
    border-top: 0;
    position: fixed;
    z-index: 12;
    height: 64px;
    top: 0;
    left: 0;
    right: 0;
  }
}
